@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

*{
  --bgcolor: hsl(111, 32%, 72%);
  --countrycolor: white;
  --fontcolor: hsl(0, 0%, 98%);
  margin: 0;
  font-family: "Quicksand";
  box-sizing: border-box;
}

.home {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
}
.card-preview{
  padding: 20px;
  color: black;
  background-color: #eff2f8 ;
  margin: 20px;
  border-radius: 10px;
}
/* .card-preview:hover{
  background-color: blueviolet;
  color: blue;
  width: 700px; 
} */
.inf{
  margin-bottom: 30px;
  font-size: 50px;
  font-weight: 600;
  text-align: center;
}
pre{
  font-size: 20px;
  font-weight: 300;
}
h2{
  margin-left: 50px;
  font-size: 30px;
  margin-bottom: 20px;
}
.task{
  text-align: center;
  margin: 10px; 
  font-size: 45px;
}
@media (min-width: 375px) {
  .card-preview {
      grid-template-columns: repeat(1, 1fr);
  }
}

@media (min-width: 640px) {
  .card-preview {
      grid-template-columns: repeat(1, 1fr);
  }
}

@media (min-width: 860px) {
  .card-preview {
      grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1020px) {
  .card-preview {
      grid-template-columns: repeat(2, 1fr);
  }
}